.App {
  text-align: center;
  box-sizing: border-box;
}

div{
  font-family: 'SpoqaHanSans';
  display:block;
}
.nav-bar{
  font-family: 'SpoqaHanSans';
  color:black;
}

.product-page-ad{
  height:150px;
  padding:30px;
}


.modal-header{
  align-items:center;
  justify-content: center;
  flex-wrap:wrap;
  margin:0px;
  padding:0px;
  justify-content: space-between;
  border:none;
}

.right-info{
  position:sticky;
  top:50px;
}

.right-ad{
  border : 1px solid #bbb;
  padding:20px;

}

.price-info{
  border : 1px solid #61dafb;
  border-radius: 5px;
}

.top-nav{
  Color:black;
  font-size: 13px;
}

.scroll-top-btn{
  width:50px;
  height:50px;
  color: rgba(130, 115, 185, 0.8);
  border-radius: 50px;
  position:fixed;
  bottom:30px;
  z-index: 5;
  font-size: 50px;
  opacity:0.8;
}
.close-btn{
  position:absolute;
  top:5px;
  right:5px;
  margin:5px;
}
.modal-content{
  width:80%;
  margin:auto;
  padding:0px;
  border-radius: 10px;
}

.close{
  position:absolute;
  top:5px;
  right:5px;
}

.login-form-group{
  width:100%;
  margin-top:30px;
}
.login-form{
  width:100%;
  padding:20px;
}

.footer-link p{
  text-align: left;
  margin-bottom:5px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
